.breed-select {
  width: 200px;
  height: 30px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.hide, .error {
  display: none;
}

.error.show {
  color: red;
  display: block;
}

.loader {
  width: 0;
  height: 4.8px;
  box-sizing: border-box;
  background: #00f;
  animation: animFw 8s linear infinite;
  display: flex;
  position: relative;
  box-shadow: 0 0 10px rgba(255, 255, 255, .5);
}

.loader:after, .loader:before {
  content: "";
  width: 10px;
  height: 1px;
  opacity: 0;
  box-sizing: border-box;
  background: #00f;
  animation: coli1 .3s linear infinite;
  position: absolute;
  top: 9px;
  right: -2px;
  transform: rotate(-45deg)translateX(0);
}

.loader:before {
  animation: coli2 .3s linear infinite;
  top: -4px;
  transform: rotate(45deg);
}

@keyframes animFw {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

@keyframes coli1 {
  0% {
    opacity: .7;
    transform: rotate(-45deg)translateX(0);
  }

  100% {
    opacity: 0;
    transform: rotate(-45deg)translateX(-45px);
  }
}

@keyframes coli2 {
  0% {
    opacity: 1;
    transform: rotate(45deg)translateX(0);
  }

  100% {
    opacity: .7;
    transform: rotate(45deg)translateX(-45px);
  }
}

